import { evntBus } from "../../bus";
export default {
  data: () => ({
    items_view: "list",
    item_group: "ALL",
    favourites_view: false,
    loading: false,
    items_group: ["ALL"],
    items: [],
    search: "",
    itemsPerPage: 1000,
      items_headers: [
        {
          text: "Name",
          align: "start",
          sortable: true,
          value: "item_name",
        },
        { text: "Code", value: "name", align: "start" },
        { text: "Group", value: "item_group", align: "start" },
        { text: "Price", value: "price", align: "start" },
        { text: "VAT", value: "vat", align: "start" },
      ],
  }),

  methods: {
    get_items() {
      const vm = this;
      this.loading = true;
      if (localStorage.items_storage) {
        vm.items = JSON.parse(localStorage.getItem("items_storage"));
        vm.loading = false;
      }
      frappe.call({
        method: "posawesome.posawesome.page.posapp.posapp.get_items",
        args: {},
        callback: function (r) {
          if (r.message) {
            const loadItmes =
              !localStorage.items_storage ||
              JSON.parse(localStorage.getItem("items_storage")).length !=
                r.message.length;
            localStorage.setItem("items_storage", "");
            localStorage.setItem("items_storage", JSON.stringify(r.message));
            if (loadItmes) {
              vm.$nextTick(() => {
                console.log("loadItmes", loadItmes);
                vm.items = JSON.parse(localStorage.getItem("items_storage"));
                vm.loading = false;
              });
            }
          }
        },
      });
    },
    get_items_groups() {
      const vm = this;
      frappe.call({
        method: "posawesome.posawesome.page.posapp.posapp.get_items_groups",
        args: {},
        callback: function (r) {
          if (r.message) {
            r.message.forEach((element) => {
              vm.items_group.push(element.name);
            });
          }
        },
      });
    },
    add_item(item) {
      evntBus.$emit("add_item", item);
    },
    enter_event() {
      if (!this.filtred_items.length || !this.search) {
        return;
      }
      this.add_item(this.filtred_items[0]);
      this.search = null;
    },
    esc_event() {
      this.search = null;
    },
  },
  computed: {
    filtred_items() {
      let filtred_list = [];
      let filtred_group_list = [];
      if (this.item_group != "ALL") {
        filtred_group_list = this.items.filter((item) =>
          item.item_group.toLowerCase().includes(this.item_group.toLowerCase())
        );
      } else {
        filtred_group_list = this.items;
      }
      if (!this.search || this.search.length < 3) {
        return (filtred_list = filtred_group_list.slice(0, 100));
      } else if (this.search) {
        filtred_list = filtred_group_list.filter((item) =>
          item.item_name.toLowerCase().includes(this.search.toLowerCase())
        );
        if (filtred_list.length == 0) {
          filtred_list = filtred_group_list.filter((item) =>
            item.item_code.toLowerCase().includes(this.search.toLowerCase())
          );
        }
      }
      return filtred_list.slice(0, 100);
    },
  },

  created: function () {
    this.$nextTick(function () {
      this.get_items();
      this.get_items_groups();
    });
  },
  // mounted() {
  //   if (localStorage.getItem('items_storage')) {
  //     try {
  //       this.items = JSON.parse(localStorage.getItem('items_storage'));
  //     } catch(e) {
  //       // localStorage.removeItem('cats');
  //       console.log(e)
  //     }
  //   }
  // },
};
